
.sidebar {
  display: block;
  position: absolute;
  width: 150px;
  left: 0;
  top: 50px;
  bottom: 0;
  background: #2E363F;
  overflow: hidden;
}
.sidebar .el-menu-item {
    display: block;
}
.sidebar ul {
    height: 100%;
}
.sidebar .toggle-sidebar {
    text-align: center;
    height: 30px;
    line-height: 30px;
    color: #fff;
    cursor: pointer;
}
.sidebar .el-menu [class^="el-icon-"] {
    vertical-align: baseline;
    margin-right: 10px;
}
.sidebar .el-menu-item {
    padding: 0 20px !important;
}
.sidebar .el-menu-item, .sidebar .el-submenu__title {
    height: 45px !important;
    line-height: 45px !important;
}
.sidebar-collapsed .el-menu__text {
  display: none;
}



